	module.exports = {"data":{"markdownRemark":{"html":"<p>You can submit your found vulnerabilities to programs by submitting reports. </p>\n<p>In order to submit reports: </p>\n<ol>\n<li>Go to your program's security page.\n<img src=\"https://github.com/Hacker0x01/docs.hackerone.com/blob/master/docs/hackers/images/report-submissions-1.png?raw=true\" alt=\"report-submissions-1\"></li>\n<li>Click the green <strong>Submit Report</strong> button. </li>\n<li>Select the asset type of the vulnerability on the <strong>Submit Vulnerability Report</strong> form.\n<img src=\"https://github.com/Hacker0x01/docs.hackerone.com/blob/master/docs/hackers/images/report-submissions-2.png?raw=true\" alt=\"report-submissions-2\"></li>\n<li>Select the weakness or the type of potential issue you've discovered.\n<img src=\"https://github.com/Hacker0x01/docs.hackerone.com/blob/master/docs/hackers/images/report-submissions-3.png?raw=true\" alt=\"report-submissions-3\"></li>\n<li><em>(Optional)</em> Select the severity of the vulnerability. You can use the CVSS calculator to determine the severity.\n<img src=\"https://github.com/Hacker0x01/docs.hackerone.com/blob/master/docs/hackers/images/report-submissions-4.png?raw=true\" alt=\"report-submissions-4\"></li>\n<li>Write up your proof of concept. State: <br>* What the vulnerability is <br>* The steps to reproduce the vulnerability <br>* What kind of impact an attacker can make if they were to exploit the vulnerability<br>\n<img src=\"https://github.com/Hacker0x01/docs.hackerone.com/blob/master/docs/hackers/images/report-submissions-5.png?raw=true\" alt=\"report-submissions-5\"></li>\n<li><em>(Optional)</em> Attach screenshots, demo videos, or any other helpful material. </li>\n<li>Click <strong>Submit Report</strong>. </li>\n</ol>\n<p>After you've submitted your report, you must wait for programs to respond to your submissions. </p>","frontmatter":{"path":"/hackers/submitting-reports","title":"Submitting Reports"}}},"pathContext":{}}

